@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a, .no-underline {
  text-decoration: none !important;
}

.app {
  font-family: "Roboto";
  overflow-x: hidden;
}

.custom-nav {
  padding: 0;
  background-color: #0b3c60;
  border-bottom: 2px solid #0b3c60;
  border-top: 2px solid #0b3c60;
  box-shadow: 1px 1px 10px;
}

.fullscreen {
  height: 100vh;
  width: 100vw;
}

.creame-background {
  background-color: #fffff8;
}

.nav-icon {
  height: 40px;
  width: 60px;
  padding-left: 5px;
}

.centered-title {
  font-size: 40px !important;
  text-align: center;
  font-weight: 400;
  text-shadow: 1px 1px 3px #777777;
}

.smaller-centered-title {
  font-size: 20px !important;
  text-align: center;
  font-weight: 300;
  text-shadow: 1px 1px 3px #777777;
}

.bullet {
  background: url(data:image/png;base64,) no-repeat left top;
  padding-top: 7px;
  padding-left: 44px;
}

.bullet-list {
  list-style-type: none;
}

.no-padding {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.box-shadow {
  box-shadow: 3px 3px 8px #777777;
}

.grow {
  -webkit-transition: all 0.1s ease-in-out;
  font-size: 20px;
}

.grow:hover {
  -webkit-transform: scale(1.01);
}

.black-font {
  color: black !important;
}

.stand-out-border {
  outline: 2px solid #0b3c60;
  margin-top: 2px;
  margin-bottom: 2px;
  background-color: #fffff8 !important;
}

.stand-out-border-text {
  color: #d32c1d;
}

.blue-text {
  color: #0b3c60;
}

